export default {
	data() {
		return {
			id:0,
			phone:"",//手机号inputValue
			pass:"",//密码inputValue
			vcode:"",//验证码inputValue
			vcodeUrl:"http://localhost:8080/yiqiba/getVerify?id="+this.id
		};
	},
	methods:{
		login(){//用户点击登录调用login方法
		//1.发送post请求，参数为以上input中的value
			uni.request({
				method:"POST",
				url:"http://localhost:8080/yiqiba/login",
				data:{
					account:this.phone,
					upass:this.pass,
				},
				success(val) {
					uni.showToast({
						icon:"none",
						title:"success",
						duration:1500
					})
				},
				fail(e) {
					uni.showToast({
						icon:"none",
						title:"faild",
						duration:1500
					})
				}
			})
		},
		changeVcode(){
			var id = this.id+1;
			this.vcodeUrl += id;
		},
		complate(){
			
		}
	},
	computed:{
		
	}
}
